import React from "react";
import { useNavigate } from "react-router-dom";

const StepNavigation = ({ onNext, onBack, showBack = true, nextLabel = "Далі", customPosition }) => {
  const navigate = useNavigate();

  return (
    <>
      {showBack && (
        <div className="step-back-button">
          <button className="back-btn" onClick={onBack || (() => navigate(-1))}>
             Назад
          </button>
        </div>
      )}

      <div className={`step-next-button ${customPosition ? customPosition : ""}`}>
        <button className="next-btn" onClick={onNext}>
          {nextLabel}
        </button>
      </div>
    </>
  );
};

export default StepNavigation;
